.App {
  background-color:#111111;
  text-align: left;
}

.nav {
  background-color: #f729678e;
  position: sticky;
  top: 0px;
  width: 100%;
  height: 60px;
  z-index: 1000;
  box-shadow: 0 4px 14px 0 rgba(0, 0, 0, 0.15);
}

.nav-logo {
  height: 40px;
  width: 45px;
}

.nav-logo:hover {
  cursor: pointer;
}

.nav .nav-content {
  max-width: 900px;
  padding: 0rem 2rem;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}

.nav-item {
  display: inline;
  margin-left: 2rem;
  color: white;
}

.nav-item:hover {
  color: black;
  cursor: pointer;
}

.App-body {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 1vmin);
  color: white;
}





.section {
  border: 1px solid #c420518e;
  width: 100%;
  min-height: 800px
}

.section-dark {
  background: #111111;
  color: white;
}

.section-content {
  max-width: 920px;
  margin: 0 auto;
  padding: 40px;
}






.App-logo {
  height: 25vmin;
  pointer-events: none;
}


.App-footer {
  background-color: #111111;
  min-height: 2vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 1vmin);
  color: rgb(154, 81, 255);
}

/* Container for flexboxes */
.row {
  min-width: 30vh;
  display: -webkit-flex;
  display: flex;
}

/* Create three equal columns that sits next to each other */
.column {
  -webkit-flex: 1;
  -ms-flex: 1;
  flex: 1;
  padding: 4px;
  flex: 1 0 30%;
  display: flex;
  justify-content: center;
  align-items: center;
}


/* Responsive layout - makes the three columns stack on top of each other instead of next to each other */
@media (max-width: 600px) {
  .row {
    -webkit-flex-direction: column;
    flex-direction: column;
  }
}

.boxed {
  border: 1px solid rgba(255, 255, 255, 0.568);
  border-radius: 1%;
  padding: 5%;
  margin: auto;
  text-align: left;
  width: 90%;
  height: 90%;
  margin: 2%;
}

.landingText {
  position: absolute;
  top: 30%;
  left: 20%
}

.boxed:hover {
  border: 1px solid #f72967;
}

.App-link {
  color: #f72967;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}